import { Container } from "pixi.js";
import type { Sprite } from "pixi.js";

/**
 * List of separators
 */
export enum ESoundButtonPosition {
	DEFAULT = "DEFAULT",
	BOTTOM = "BOTTOM",
}

export class SoundButton extends Container {
	/**
	 * Icon used to show sound state (from the editor)
	 */
	public iconMute: Sprite = null;
	/**
	 * Icon used to show sound state (from the editor)
	 */
	public iconUnmute: Sprite = null;
	/**
	 * Icon used to show sound state (from the editor)
	 */
	public mobilePosition: ESoundButtonPosition = ESoundButtonPosition.DEFAULT;
}
