import { LoadedView, Resize } from "@gdk/core-pixi";
import { Graphics } from "pixi.js";
import type { Timeline } from "@gdk/core-pixi";

import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";
import type { IAdaptativeLoadingView } from "../../../identity";

export class LoadingView extends LoadedView implements IAdaptativeLoadingView {
	/**
	 * background generated for the view
	 */
	protected background: Graphics;
	/**
	 * Loading progress timeline (from the editor)
	 */
	public progress: Timeline = null;

	/**
	 * Constructor
	 * @param vi the adaptative visual identity
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();

		// Background
		this.background = new Graphics();
		this.background.resize = new Resize(Resize.cover);
		this.background.interactive = true;
		this.background.beginFill(0x000000, 1);
		this.background.clear();
		this.background.drawRect(0, 0, 1, 1);
		this.background.endFill();
		this.addChild(this.background);
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		// Timeline
		this.progress.visible = true;
		this.progress.setAnimations([
			{
				name: "loading",
				label: "loading",
			},
		]);
		this.progress.play(0);
		this.progress.timeline.pause();
	}

	/**
	 * Defines the function called each time a file is loaded
	 * @param percentage the percentage of files already loaded
	 */
	public onProgress(percentage: number): void {
		this.progress.timeline.progress(percentage / 100);
		this.progress.timeline.pause();
	}

	/**
	 * On the view will disappear
	 */
	public onWillDisappear(): void {
		this.visible = false;
	}
}
