import { Graphics } from "pixi.js";
import { LoadedView, Resize } from "@gdk/core-pixi";
import { TweenMax } from "gsap";
import type { Timeline } from "@gdk/core-pixi";
import type { WebGLRenderer, CanvasRenderer } from "pixi.js";

import { EVisualIdentityEvent } from "../../../events";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

export class IntroductionView extends LoadedView {
	/**
	 * introduction timeline (from the editor)
	 */
	public intro: Timeline = null;
	/**
	 * Defines if the illiko introduction should be skipped
	 */
	public skipIntro: boolean = false;

	/**
	 * White background generated for the view
	 */
	protected background: Graphics;

	private _onNextStateCallback: () => void;

	/**
	 * Constructor
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();

		// Background
		this.background = new Graphics();
		this.background.interactive = true;
		this.background.resize = new Resize(Resize.cover);
		this.background.beginFill(0x000000, 1);
		this.background.clear();
		this.background.drawRect(0, 0, 1, 1);
		this.background.endFill();
		this.addChild(this.background);
	}

	/**
	 * Animate the clip
	 * @param speed the speed affected to it
	 */
	public animate(nextState: () => void): void {
		this._onNextStateCallback = nextState;

		if (this.skipIntro) {
			this.visible = false;
			this._onNextState();
			nextState();
		} else if (this.intro.timeScale > 0.001) {
			this.intro.play();
		} else {
			this._onNextState();
			this.visible = false;
		}
	}

	/**
	 * On the view will appear
	 */
	public onWillAppear(): void {
		this.visible = true;
	}

	/**
	 * On the view disappear
	 */
	public onWillDisappear(): void {
		// Just override to the view not be set to "visible = false" as we need to keep it visible
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		// Configure
		this.intro.visible = false;
		this.intro.setAnimations([
			{
				name: "intro",
				label: "intro",
				delay: 500,
			},
			{
				name: "outro",
				label: "outro",
				delay: 1000,
			},
		]);
		this.intro.timeline.pause();

		// Intro events
		this.intro.onAnimationStart(() => {
			this.intro.visible = true;
		});
		this.intro.onComplete(() => {
			this.intro.visible = false;
			this.intro.pause();
			TweenMax.delayedCall(0.2, () => {
				this._onNextState();
				this.vi.emit(EVisualIdentityEvent.IntroductionComplete);
				this.visible = false;
			});
		});
	}

	/**
	 * Render text with WebGL
	 * @param renderer the pixi.js renderer (WebGLRenderer)
	 * @override PIXI.Sprite.renderWebGL
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		super.renderWebGL(renderer);
	}

	/**
	 * Render text with Canvas renderer
	 * @param renderer the pixi.js renderer (CanvasRenderer)
	 * @override PIXI.Sprite._renderCanvas
	 */
	public _renderCanvas(renderer: CanvasRenderer): void {
		super._renderCanvas(renderer);
	}

	// Called once an illiko introduction animation has finished
	private _onNextState(): void {
		if (this._onNextStateCallback && this.visible) {
			this._onNextStateCallback();
		}
	}
}
