import { Transform } from "pixi.js";
import { tracker, ButtonClick } from "@gdk/core";
import { LoadedView, Docking } from "@gdk/core-pixi";
import type {
	IRadialGradientStyle,
	ILinearGradientStyle,
} from "@gdk/core-pixi/ui/baseui/text/textStyle";
import type { Amount } from "@gdk/core";
import type { TextField } from "@gdk/core-pixi";
import type { Container } from "pixi.js";

import { SoundButton, ESoundButtonPosition } from "../components/soundButton";
import { SettingsComponent } from "../components/settingsComponent";
import { Logos, ELogoColor } from "../components/logos";
import { DeviceDetect } from "../components/deviceDetect";
import { EVisualIdentityEvent, EVisualIdentityAction } from "../../../events";
import type { IAdaptativeInGameView } from "../../../identity";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

export class InGameView extends LoadedView implements IAdaptativeInGameView {
	/**
	 * Button used to display rules
	 */
	public rulesButton: Container = null;
	public soundButton: SoundButton = null;
	public logos: Logos = null;
	/**
	 * Defines the settings component used to draw the settings window, buttons etc.
	 */
	public settingsComponent: SettingsComponent;
	/**
	 * Indicates if the rules are enabled
	 */
	private _rulesEnabled: boolean = true;
	/**
	 * Label textfield used to draw the purse value (from the editor)
	 */
	public purseAmount: TextField = null;
	public purseTitle: TextField = null;

	/**
	 * Constructor
	 * @param vi the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();
		this.settingsComponent = new SettingsComponent(vi);
		this.soundButton = new SoundButton();
		this.logos = new Logos();
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		// Resize
		this.vi.emitter.on("resize", () => this._resetSizes());

		// Purse management
		this.vi.emitter.on(EVisualIdentityAction.SetPurse, (purse) => this.setPurse(purse));

		// Init sound behavior
		this.initSoundButton();
		this.initRulesButton();
		this.logos.color = ELogoColor.White;

		// Change layout according to device
		if (DeviceDetect.isMobileOrTablet()) {
			this.soundButton.docking = new Docking(Docking.bottomLeft);
			this.rulesButton.docking = new Docking(Docking.bottomLeft);
		} else {
			this.soundButton.docking = new Docking(Docking.bottomRight);
			this.rulesButton.docking = new Docking(Docking.bottomRight);
		}
	}

	/**
	 * On the view will appear
	 */
	public onWillAppear(): void {
		// Reset items sizes
		this._resetSizes();
		this.visible = true;
	}

	/**
	 * Init the sound button behavior
	 */
	private initSoundButton(): void {
		if (this.vi.parameters.soundButtonEnabled ?? true) {
			this.soundButton.on("pointertap", () => {
				const soundRequiredStatus = !this.vi.game.soundStatus;

				// Emit event to the game
				this.vi.emitter.emit(EVisualIdentityAction.SoundEnabled, soundRequiredStatus);

				// the button is a sprite, so we have to manually track it
				tracker.track(
					new ButtonClick(soundRequiredStatus ? "settings:sound" : "settings:soundOff")
				);
			});
		} else {
			this.soundButton.buttonMode = false;
		}

		this.vi.emitter.on(EVisualIdentityEvent.SoundStatusChanged, () => {
			this.soundButton.iconMute.visible = this.vi.game.soundStatus;
			this.soundButton.iconUnmute.visible = !this.vi.game.soundStatus;
		});
	}

	/**
	 * Init the rules button behavior
	 */
	private initRulesButton(): void {
		this.rulesButton.on("pointertap", () => {
			if (!this.rulesEnabled) {
				return;
			}
			if (this.rulesVisible) {
				// Emit event to the game
				this.vi.emitter.emit(EVisualIdentityEvent.HideRules);
				// the button is a sprite, so we have to manually track it
				tracker.track(new ButtonClick("settings:rulesOff"));
			} else {
				// Emit event to the game
				this.vi.emitter.emit(EVisualIdentityEvent.ShowRules);
				// the button is a sprite, so we have to manually track it
				tracker.track(new ButtonClick("settings:rules"));
			}
		});
	}

	/**
	 * Returns if the rules are visible
	 */
	public get rulesVisible(): boolean {
		return this.vi.game.rulesVisible;
	}

	/**
	 * Returns if the rules are enabled
	 */
	public get rulesEnabled(): boolean {
		return this._rulesEnabled;
	}

	/**
	 * Sets if the rules are enabled
	 */
	public set rulesEnabled(enabled: boolean) {
		this._rulesEnabled = enabled;
	}

	/**
	 * Resets the bottom overlay by taking the game's dimensions
	 */
	private _resetSizes(): void {
		// Force updating bounds
		this.getBounds();

		// Get world transform
		const transform = new Transform();
		transform.setFromMatrix(this.worldTransform);

		this.rulesButton.scaleXY = 1 / transform.scale.x / 2;
		this.soundButton.scaleXY = 1 / transform.scale.x / 2;
		this.logos.scaleXY = 1 / transform.scale.x / 2;
		this.logos.docking.x = 6 / transform.scale.x;
		this.logos.docking.y = 18 / transform.scale.x;

		this.purseTitle.docking.x = -15 / transform.scale.x;
		this.purseTitle.docking.y = 25 / transform.scale.x;
		this.purseTitle.styles.default.fontSize = Math.round(14 / transform.scale.x);
		this.purseTitle.update();

		this.purseAmount.docking.x = -15 / transform.scale.x;
		this.purseAmount.docking.y = 40 / transform.scale.x;
		this.purseAmount.styles.default.fontSize = Math.round(14 / transform.scale.x);
		this.purseAmount.update();

		if (DeviceDetect.isMobileOrTablet()) {
			this.rulesButton.docking = new Docking(Docking.bottomLeft);
			this.rulesButton.docking.x = 50 / transform.scale.x;
			this.rulesButton.docking.y = -6 / transform.scale.x;
			if (this.soundButton.mobilePosition === ESoundButtonPosition.BOTTOM) {
				this.soundButton.docking = new Docking(Docking.bottomLeft);
				this.soundButton.docking.x = 100 / transform.scale.x;
				this.soundButton.docking.y = -6 / transform.scale.x;
			} else {
				this.soundButton.docking = new Docking(Docking.bottomLeft);
				this.soundButton.docking.x = 6 / transform.scale.x;
				this.soundButton.docking.y = -50 / transform.scale.x;
			}
		} else {
			this.soundButton.docking = new Docking(Docking.bottomRight);
			this.soundButton.docking.x = -6 / transform.scale.x;
			this.soundButton.docking.y = -6 / transform.scale.x;
			this.rulesButton.docking = new Docking(Docking.bottomRight);
			this.rulesButton.docking.x = -50 / transform.scale.x;
			this.rulesButton.docking.y = -6 / transform.scale.x;
		}
	}

	/**
	 * Sets the purse
	 * @param purse the purse to set
	 */
	public setPurse(value: Amount): void {
		this.purseAmount.text = value
			? value.toString({ symbolFormatName: "postspace", minimumFractionDigits: 2 })
			: "-";
	}

	/**
	 * Sets the purse
	 * @param purse the purse to set
	 */
	public setPurseColor(
		color: string | CanvasGradient | ILinearGradientStyle | IRadialGradientStyle
	): void {
		this.purseAmount.styles.default.fill = color;
		this.purseAmount.update();
		this.purseTitle.styles.default.fill = color;
		this.purseTitle.update();
	}
}
