import { LockView as fdjLockView } from "../../fdj/views/lockView";
import { LockPict } from "../components/lockPict";
import { DeviceDetect } from "../components/deviceDetect";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

export class LockView extends fdjLockView {
	public portrait: LockPict = new LockPict();
	public landscape: LockPict = new LockPict();

	/**
	 * Constructor
	 * @param vi the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		super.onLoad();
		// Check if we are on desktop or mobile
		if (DeviceDetect.isMobileOrTablet()) {
			this.portrait.desktop.visible = false;
			this.landscape.desktop.visible = false;
			this.portrait.mobile.visible = true;
			this.landscape.mobile.visible = true;
		} else {
			this.portrait.desktop.visible = true;
			this.landscape.desktop.visible = true;
			this.portrait.mobile.visible = false;
			this.landscape.mobile.visible = false;
		}
	}
}
