import { Container } from "pixi.js";
import type { Sprite } from "pixi.js";

/**
 * List of separators
 */
export enum ELogoColor {
	Green = 1,
	White = 2,
	Black = 3,
}

/**
 * Different version of the logo
 */
export class Logos extends Container {
	/**
	 * Black logo
	 */
	public black: Sprite = null;
	/**
	 * Green logo
	 */
	public green: Sprite = null;
	/**
	 * White logo
	 */
	public white: Sprite = null;

	/**
	 * Constructor
	 */
	public constructor() {
		super();
	}

	/**
	 * Sets the color of the logo
	 */
	public set color(color: ELogoColor) {
		this.green.visible = color === ELogoColor.Green;
		this.black.visible = color === ELogoColor.Black;
		this.white.visible = color === ELogoColor.White;
	}
}
