import { Popup } from "@gdk/core-pixi";

import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

/**
 * We have to keep this component even if it is empty due to compatibility reasons
 */
export class SettingsComponent extends Popup {
	private _rulesEnabled: boolean = false;

	/**
	 * Constructor
	 * @param vi: the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();
	}

	/**
	 * Called by the project loader once the container has
	 * been imported
	 */
	public onLoaded(): void {
		// Force unvisible
		this.visible = false;
	}

	/**
	 * Shows the settings component
	 * Force unvisible - There is nothing to display
	 */
	public show(): void {
		// Force unvisible
		this.visible = false;
	}

	/**
	 * Returns if the rules buttons are enabled
	 */
	public get rulesEnabled(): boolean {
		return this._rulesEnabled;
	}

	/**
	 * Sets if the rules buttons are enabled
	 */
	public set rulesEnabled(enabled: boolean) {
		this._rulesEnabled = enabled;
	}

	/**
	 * Force unvisible - There is nothing to display
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set visible(visible: boolean) {
		// Do nothing to force unvisibility
	}
}
